.bg {
  position: relative;
  background: url(./asset/img1.jpg);
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bg::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); 
  z-index: 1;
}

.App {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  align-items: center;
  color: #fff; 
  text-align: center; 
  padding: 20px;
}

h1 {
  
  font-weight: bold; 
  color: #fff;
  font-family:'Times New Roman', Times, serif;
}


input {
  padding: 10px;
  margin-right: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  text-align: center;
}

button {
  padding: 10px 15px;
  border-radius: 5px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  font-weight: bold; 
  margin: 20px;
}

button:hover {
  background-color: #0056b3;
}

h2 {
  margin-top: 20px;
  font-weight: bold; 
  color: #fff;
   font-family: Arial, Helvetica, sans-serif;
}

p {

  font-weight: bold; 
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;

}
p span{
  color: rgb(255, 255, 255);
}